*{
    font-family: "Poppins,Sans-serif";
    /* overflow-x: hidden; */
    
}
#data{
    width: 95%;
    margin: auto;
    display: block;
    z-index: 2;
    box-shadow: 0 0 50px 15px #270c88;
    border-radius: 15px;
}

#nav-menu{
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}

@media  (min-width: 900px){
    #nav-menu{
        position: sticky;
        top: 0;
        z-index: 100;    
        width: 95%;
        margin: auto;
        color: goldenrod;
        display: block;
        -webkit-backdrop-filter: blur(8px);
        backdrop-filter: blur(10px);
    }
    
    
    #nav-menu .nav-list{
      justify-content:  center;
      padding: 20px;
      
    }
    
    .items{
        gap: 40px;
    }
    .bread{
      color: goldenrod;
      font-size: 24px;
      font-weight: 600;
    }
}

@media  (min-width: 599px)  and (max-width: 899px) {
    #nav-menu{
        position: sticky;
        top: 0;
        z-index: 100;    
        width: 95%;
        color: goldenrod;
        margin: auto;
        display: block;
        -webkit-backdrop-filter: blur(8px);
        backdrop-filter: blur(8px);
    }
    
    
    #nav-menu .nav-list{
      justify-content: center;
      padding: 5px;
      margin: auto;
      padding:"25px 0px 25px 0px" ;
    }
    
    .items{
        gap: 15px;
    }

    .bread{
      color: goldenrod;
      font-size: 22px;
      font-weight: 500;
      margin: auto;
    }
}
@media (max-width: 598px) {
    #nav-menu{
        position: sticky;
        top: 0;
        z-index: 100;    
        width: 100%;
        margin: auto;
        display: block;
        color: goldenrod;
        -webkit-backdrop-filter: blur(8px);
        backdrop-filter: blur(8px);
    }
    
    
    #nav-menu .nav-list{
      justify-content: center;
      margin: auto;
      padding: 20px 0px 10px 0px;
    }
    
    .items{
        gap: 8px;
    }

    .bread{
      color: goldenrod;
      font-size: 14px;
      font-weight: 300;
      margin: auto;
    }
}