#home{
    font-family: "Poppins,Sans-serif";
    margin: auto;
    margin-top: 20px;
    border-radius: 15px;
    width: 100%;
    height: 500px;
    overflow-x: hidden;
}

.back{
    width: 100%;
    height: 100%;
    border-radius: 15px;
    -webkit-filter: blur(5px);
    filter: blur(5px);
}

#home .content{
    position: absolute;
    justify-content: center;
    text-align: center;
    border-radius: 15px;
    margin: auto;
    margin-top: -35%;
    width: 100%;
    z-index: 2;
}
#about{
    background-image: url(../Assets/particles2.jpg);
    width: 100%;
    overflow-x: hidden;
}

.head-image{
    width: 250px;
    height: 250px;
    margin: auto;
}
  
#skills{
    width: 100%;
    margin: auto;
    color: goldenrod;
    overflow-x: hidden;
    border-radius: 20px;
}

@media (min-width:501px) and  (max-width: 1000px){
    #home {
        display: block;
        justify-content: center;
        margin: auto;
    }


}
@media  (max-width: 500px){
    #home {
        display: block;
        justify-content: center;
        padding: 0;
        margin: auto;
    }
}