#skills-container{
    width: 95%;
    position: absolute;
    border-radius: 20px;
    margin: auto;
    margin-top: -56%;
    /* border: 1px solid red; */
    justify-content: center;
    align-items: center;
    z-index: 2;
}
.bgcont{
    width: 100%;
    /* border: 1px solid greenyellow; */
    height: 800px;
    -webkit-filter: blur(5px);
    filter: blur(5px);
}

.bgcont .bgimg{
    height: 100%;
}


.skill-logo{
    margin: auto;
    width: 100px;
    height: 100px;
    margin-bottom: 5px;
}

.tools-box{
    width: 130px;
    /* margin: 20px; */
    margin-bottom: 50px;
    height: 160px;
    box-shadow: 0 0 20px 10px #ecc902;
    justify-content: center;
    padding-top: 15px;
    border-radius: 10px;
    /* border: 1px solid red; */
    align-items: center;
    cursor: pointer;
    background-color: black;
    font-size: 20px;
}